.patternPannel-title{
  font-family: Lato;
  font-size: 2vw;
  font-weight: 300;
}
.patternPannel{
  padding-left: 3%;
  padding-right:3%;
  text-align: center;
}
.patternPannel-patterns{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.pattern-icon-wrapper{
  width: 16%;
  padding-bottom: 16%;
  position: relative;
}
.pattern-icon-wrapper img{
  position: absolute;
  top:0;
  left: 0;
}
.patternImg{
  width:100%;
  opacity: 1;
	-webkit-animation-name: fadeInOpacity;
	        animation-name: fadeInOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
  -webkit-transition: ease all 0.5s;
  -o-transition: ease all 0.5s;
  transition: ease all 0.5s;
}
@-webkit-keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.patternPannel-footer{
  margin-left: 25%;
  margin-right: 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.patternPannel-footer-part img{
  width : 1.6vw;
}
.patternPannel-footer-part span{
  padding-left: 0.2vw;
  font-size: 0.5vw;
  font-weight: bold;
}



