.equalizer-controller{
  width: 100%;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.equalizer-controller-wrapper{
  width: 90%;
  overflow: hidden;
  position: relative;
  margin: auto;
}

.equalizer-panel-over{
  width: 100%;
  pointer-events: none;
}

.equalizer-value-box-container{
  position: absolute;
  top: 2%;
  height:96%;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}

.equalizer-value-box{
  width: 91%;
  height: 9.8%;
  margin-bottom: 1%;
  margin-right: 4%;
  -ms-flex-item-align: end;
      align-self: flex-end;
  opacity: 0.5;
  border-radius: 7%;
}

.equalizer-panel-button-wrapper{
  overflow: hidden;
  position: relative;
  margin-top: 55%;
  margin-bottom: 5%;
}

.equalizer-button-plus-wrapper{
  position: absolute;
  left: 53%;
  top: 13%;
  width: 33%;
  height: 50%;
  cursor: pointer;
}



.equalizer-button-minus-wrapper{
  position: absolute;
  left: 12%;
  top: 15%;
  width: 33%;
  height: 50%;
  cursor: pointer;
}

.equalizer-panel-button-plus{
  position: absolute;
  width: 80%;
  left: 10%;
  top: 30%;
}

.equalizer-panel-button-minus{
  width: 80%;
  position: absolute;
  left: 15%;
  top: 40%;
}

.equalizer-controller-title{
  font-family: Lato;
  width: 100%;
  top: 70%;
  font-size: 0.8vw;
  text-align: center;
}

.equalizer-controller-title p{
  margin-bottom: 0;
}
.equalizer-panel-button{
  width: 100%;
  pointer-events: none;
}



